import React, { Component } from 'react';
import axios from 'axios';
import Movies from './Movies';
import './Section.scss';



class Section extends Component {
    state = {
        kim : []        //키값은 kim 
    }

    //---------------------------------3차적으로 데이터를 받고 데이터를 배열로 변환
    getMovies = async () =>{    //기본방식은 비동기처리 async () 내장함수
        const { data : {data : {movies}} } = await axios.get("https://yts.mx/api/v2/list_movies.json")  //비구조화 할당 데이터로서 
        //해석하자면 객체 안에 데이터객체의 state 의 키값 movies 를 지목 데이터는 API 의 정해진값 movies안에 들어있다
        console.log(movies) // 14번줄의 data : movies 키값 을 출력해보면 데이터는 movies안에 들어있다
        this.setState({ //데이터를 배열로 변환 9번줄 의 배열로 저장
          kim : movies   //앞에 kim는 받는 데이터 뒤에 movies는 배열로 변환된 데이터
      })
    }
    //-----------------------2차적으로 랜더함수가 끝나면 처리할 함수
    componentDidMount(){   //render() 뒤에 실행될함수를 콜백함수로 처리하는 리액트 내장함수
        this.getMovies()
    }
    //--------------------1차적으로 기본 JSX를 랜더링
    render() {
        const { kim } = this.state;    //비구조화 할당데이터 문법에 근거해서 state배열에서 kim의 키값을 배열을 받는 변수로 할당
        return (
            <section className="container cf">
                    {
                        kim.map( (data)=> ( //여기서의 data 는 매개변수로서 함수 getMovies()로 받은 데이터를 직접적으로 받는 변수역할
                            <Movies key={data.id} year={data.year} title={data.title} summary={data.summary} poster={data.medium_cover_image} genres={data.genres}/> //map()와 props Component를 사용해서 데이터데입
                          ) )
                    }
            </section>
        );
    }
    //---------------마지막으로
}

export default Section;