import React, { Component } from 'react';
import './Movies.scss';








function Movies ({year,title,summary,poster,genres}) {

    
    // mouseover.document.getElementsByClassName("movies_summary").classList.add("on")

        return (
            <div className="movies">
                <img src={poster} alt ={title}/>
                <div className="movies_data">
                    <h3 className="movies_title">{title.slice(0,10)}</h3>
                        <h5 className="movies_year">{year}</h5>
                        <ul>
                            {
                                genres.map( ( 장르값 , 키값 ) => (<li key={ 키값 }> / { 장르값 }</li>) )
                            }
                        </ul> 
                    <p className="movies_summary">{summary.slice(0,50)}...</p>
                </div>
            </div>
        );
}
//slice
export default Movies;